@Component({})
export default class PushInPage extends Vue {
     orderState:string='';
    //  isOk: boolean = true;
     model:any={
         amount:'',
         type:'',
         unit:'',
         supplyName:'',
         fileName:'',
         who:'',
         when:''
     }
     get isOk(){
        if(this.model.supplyName&&this.model.who&&this.model.fileName&&this.model.when&&this.model.amount){
            if(this.numberCheck()){
                 return true;
            }
        }else{
            return false;
        }
     }

       
    numberCheck(){
        const reg = /^\d{1,}$/;
        return reg.test(this.model.amount);
    }

     orderStateList: any = [
        
        {
            id: '0',
            name: '待审核',
        },
        {
            id: '1',
            name: '待接单',
        }]

    orderStateSearch(){}
    submit(){
        console.log(this.model)
        this.numberCheck();
    }
}

